export default {
  name: "page-field",
  data() {
    return {
      isShowMenu: false,
      options: [
        {
          label: "",
          value: "sex",
          type: "lv1"
        },
        {
          label: "",
          value: "country",
          type: "lv2"
        },
        {
          label: "",
          value: "traffic",
          type: "lv3"
        },
        {
          label: "",
          value: "filter",
          type: "filter"
        }
      ],
      type: "",
      menuDatas: [],
      subMenuDatas: [],
      subParent: {},
      grandMenuDatas: [],
      grandParent: {},
      filterMenuDatas: [],
      multiValue: "",
      multiValue_display: "",
      menuParent: {},
      sexValue: "",
      lv2Selected:'',
      lv2SelectedId:'',
      // countryValue: "",
      trafficValue: "",
      bodyHeight: ""
    };
  },
  components: {},
  mounted() {},
  watch: {
    sexValue: function(newData, oldData) {
      var that = this;
      that.changeSelectValus(newData, that.menuDatas);
    },
    // countryValue: function(newData, oldData) {
    //   var that = this;
    //   that.changeSelectValus(newData, that.subMenuDatas);
    // },
    trafficValue: function(newData, oldData) {
      var that = this;
      that.changeSelectValus(newData, that.grandMenuDatas);
    },
    lv2SelectedId:function(newData, oldData){
      var that = this;
      that.changeSelectValus(newData);
    }
  },
  created() {
    this.bodyHeight = (screen.height || document.body.clientHeight) + "px";
  },
  methods: {
    changeSelectValus: function(newData, arr) {
      var that = this;
      var selectItem = {};
      this.isShowMenu = false;
      if (arr) {
        arr.forEach(function(item) {
          if (item.value === newData) {
            that.$set(item, "active", true);
            selectItem = item;
          } else {
            that.$set(item, "active", false);
          }
        });
      }else {
        selectItem = {
          label:newData.title,
          value:newData.id
        };
      }
      that.options.forEach(function(ele) {
        if (ele.active) {
          ele.active = false;
          ele.label = selectItem.originLabel?selectItem.originLabel:selectItem.label;
        }
      });
    },
    getSelectedButtons: function(item) {
      this.menuParent = item;
      this.type = item.type;
      this.isShowMenu = true;
      if (
        this.menuDatas[0] &&
        this.menuDatas[0].value.indexOf(item.value) > -1
      ) {
        //   
      } else {
        this.subMenuDatas = [];
        this.grandMenuDatas = [];
        if (item.value === "sex") {
          this.menuDatas = [
            {
              label: "",
              value: "sexAll",
              originLabel:'',
              originValue:'sex'
            },
            {
              label: "",
              value: "man"
            },
            {
              label: "",
              value: "woman"
            }
          ];
        } 
        // else if (item.value === "country") {
        //   this.menuDatas = [
        //     {
        //       label: "",
        //       value: "countryAll",
        //       originLabel:'',
        //       originValue:'country'
        //     },
        //     {
        //       label: "",
        //       value: "China"
        //     },
        //     {
        //       label: "",
        //       value: "American"
        //     }
        //   ];
        // } 
        else if (item.value === "traffic") {
          this.menuDatas = [
            {
              label: "",
              value: "trafficAll",
              originLabel:'',
              originValue:'traffic'
            },
            {
              label: "",
              value: "metro"
            }
          ];
        } else if (item.value === "filter") {
          this.filterMenuDatas = [
            {
              name: "1",
              id: "1time"
            },
            {
              name: "2",
              id: "2time"
            },
            {
              name: "3",
              id: "3time"
            },
            {
              name: "4",
              id: "4time"
            },
            {
              name: "5",
              id: "5time"
            },
            {
              name: "6",
              id: "6time"
            }
          ];
        }
      }
    },
    setSelected: function(param) {
      var that = this;
      that.$nextTick(function() {
        that.menuDatas.forEach(function(item) {
          that.$set(item, "active", false);
        });
        that.$set(param, "active", true);
        if (this.type != "lv1") {
          if (param.value.indexOf("All") > -1) {
            //
            // if (this.subMenuDatas.length > 0) {
            //   that.subMenuDatas.forEach(function(ele) {
            //     if (ele.active) {
            //       ele.active = false;
            //     }
            //   });
            // }
            // if (that.grandMenuDatas.length > 0) {
            //   that.grandMenuDatas.forEach(function(ele) {
            //     if (ele.active) {
            //       ele.active = false;
            //     }
            //   });
            // }
            that.subMenuDatas = [];
            that.grandMenuDatas = [];
            // that.countryValue = '';
            that.trafficValue = '';
            var returnData = {
              node: {
                label: param.originLabel,
                value: param.originValue
              }
            };
            that.postEventToParent(returnData);
          } else {
            //
            // if (param.value === "China") {
            //   that.subMenuDatas = [
            //     {
            //       label: "",
            //       value: "js"
            //     },
            //     {
            //       label: "",
            //       value: "sd"
            //     }
            //   ];
            // } else if (param.value === "American") {
            //   that.subMenuDatas = [
            //     {
            //       label: "",
            //       value: "ny"
            //     },
            //     {
            //       label: "",
            //       value: "jjs"
            //     }
            //   ];
            // } else 
            if (param.value === "metro") {
              that.subMenuDatas = [
                {
                  label: "",
                  value: "yhx"
                },
                {
                  label: "",
                  value: "shx"
                }
              ];
            }
            that.subParent = param;
            //
            that.subMenuDatas.forEach(function(item) {
              that.$set(item, "active", false);
            });
          }
        } else {
          if (param.value.indexOf("All") > -1) {
            var returnData = {
              node: {
                label: param.originLabel,
                value: param.originValue
              }
            };
          } else {
            var returnData = {
              node: param
            };
          }
          that.postEventToParent(returnData);
        }
      });
    },
    setSubSelected: function(item) {
      var that = this;
      that.$nextTick(function() {
        that.subMenuDatas.forEach(function(item) {
          that.$set(item, "active", false);
        });
        that.$set(item, "active", true);
        if (this.type == "lv3") {
          //
          if (item.value === 'yhx') {
            this.grandMenuDatas = [
              {
                label: "",
                value: "njn"
              },
              {
                label: "",
                value: "hdq"
              }
            ];
          }else if (item.value === 'shx'){
            this.grandMenuDatas = [
              {
                label: "",
                value: "hhdx"
              },
              {
                label: "",
                value: "jc"
              }
            ];
          }
          this.grandParent = item;
          //
          this.grandMenuDatas.forEach(function(item) {
            that.$set(item, "active", false);
          });
        } else {
          var returnData = {
            node: item,
            parentNode: this.subParentOptions
          };
          this.postEventToParent(returnData);
        }
      });
    },
    postEventToParent: function(data) {
      var that = this;
      if (that.type != "filter") {
        that.options.forEach(function(ele) {
          if (ele.active) {
            ele.active = false;
            ele.label = data.node.label;
          }
        });
      }
      this.isShowMenu = false;
    },
    resetFilter: function() {
      this.multiValue = "";
    },
    sureFilter: function() {
      console.log(this.multiValue);
    },
    cancel:function(){
        this.isShowMenu = false;
    },
    cellClick:function(param,item){
      this.lv2SelectedId = item;
    }
  }
};



