export default {
  name: 'page-checklist',

  data() {
    return {
      options1: [],
      options2: [],
      options3: [],
      options4: [],
      value1: "",
      value2: 'A',
      value3: 'A',
      value4: ''
    };
  },
  methods: {
      blank(){
        console.log('')
      }
  },

  created() {
    this.options1 = ['A', 'B', 'C'];
    this.options3 = ['A', 'B', 'C', 'D'];
    this.options4 = ['A', 'B', 'C', 'D'];
    this.options2 = [
      {
        label: '',
        value: 'F',
        disabled: true,
      },
      {
        label: '',
        value: '',
        disabled: true
      },
      {
        label: 'A',
        value: 'A'
      },
      {
        label: 'B',
        value: 'B'
      }
    ];
  }
};



