import { Toast } from 'src/index';
import axios from 'axios'
export default {
  data() {
    return {
      year: new Date(),
      area: '',
      title2: '',
      title3: '1',
      title4: '2',
      column1: ['', '', '', '', ''],
      column2: [
        { text: '', disabled: true },
        { text: '' },
        { text: '' }
      ],
      column3:[{
        values:[
          {text:'',disabled:false,id:101,pid:-1}, 
          {text:'',disabled:false,id:201,pid:-1},
          {text:'',disabled:false,id:301,pid:-1}],
        className:'className1',
        defaultIndex: 2
      },{
        values:[
          {text:'',disabled:false,pid:101,id:10101}, 
          {text:'',disabled:true,pid:101,id:10102},
          {text:'',disabled:false,pid:101,id:10103},
          {text:'',disabled:false,pid:101,id:10104},
          {text:'',disabled:false,pid:101,id:10105},
          {text:'',disabled:false,pid:201,id:20101}, 
          {text:'',disabled:false,pid:201,id:20102},
          {text:'',disabled:false,pid:201,id:20103},
          {text:'',disabled:false,pid:201,id:20104},
          {text:'',disabled:false,pid:201,id:20105},
          {text:'',disabled:false,pid:301,id:30101}, 
          {text:'',disabled:false,pid:301,id:30102},
          {text:'',disabled:false,pid:301,id:30103},
          {text:'',disabled:false,pid:301,id:30104},
          {text:'',disabled:false,pid:301,id:30105}],
        className:'className2',
        defaultIndex: 2
      }],
      column4: {
        : ['', '', '', '', ''],
        : ['', '', '', '', ''],
        : ['', '', '', '', '']
      },
      toastContent: (value, index) => `${value}, ${index}`
    }
  },
  computed: {
    columns() {
      const column = this.column4;
      return [
        {
          values: Object.keys(column),
          className: 'column1'
        },
        {
          values: column[Object.keys(column)[0]],
          className: 'column2',
          defaultIndex: 2
        }
      ];
    }
  },
  methods: {
    onConfirm1(value, index ,picker) {
      Toast(`${value}, ${index}`);
    },
    onChange4(picker, values,index) {
      console.log(values, index);
      picker.setColumnValues(1, this.column4[values[0]]);
    },
    onConfirm(value,index) {
      console.log(value,index);
    },
    onCancel(value) {
      Toast('');
    },
    changeData(){
      axios.get('./mock/picker.json').then(resp => {
        let respData = resp.data
        if (respData.code == '0') {
          this.column1=respData.data;
        }
      })
    }
  }
};



